const Header_Style = {
    display : 'flex',
    justifyContent: 'left',
    alignItems: 'center',
    backgroundColor : '#888',
    height : '100px'
}

const Logo_Style = {
  fontSize : '30px',
  marginLeft : '15px'
}

const Achievement_Style = {
  marginLeft : '100px'
}

function Header(){
  return(
    <header className="header" style={Header_Style}>
      <div className="logo" style={Logo_Style}>Todo Application</div>
      <Link href="/">
      <div className=" achievement" style={Achievement_Style}>나의 성취 보기</div>
      </Link>
    </header>
  )
}

export default Header
